body {
  --blue-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-align: center;
  text-transform: uppercase;
  margin: auto 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:last-child {
  margin-right: 0;
}

.img {
  width: 40px;
  height: 40px;
  margin: 10px 0;
}

.is-active {
  color: var(--blue-color);
  margin-left: 0;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__link:hover {
  color: var(--blue-color);
}
